.home-container {
  display: flex;
  flex-direction: column;
  justify-content: center; /* Center items vertically */
  align-items: center; /* Center items horizontally */
  height: 100vh; /* Full height of the viewport */
  background-color: #ffeb3b; /* Light background color for fall */
  background-image: url('../../public/assets/orange-trees-background.jpg');
  background-size: cover;
  background-position: center;
}

.countdown {
  font-size: 5rem;
  color: darkorange;
  font-family: sans-serif; /* Playful font */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  margin-top: 20px; /* Space above the countdown */
}

.welcome {
  font-size: 2.5rem; /* Increase font size */
  color: #4a4a4a; /* Dark gray */
  font-family: 'Arial', sans-serif; /* Clean font */
  margin-bottom: 20px; /* Space below the welcome message */
  background-color: rgba(255, 255, 255, 0.8); /* Semi-transparent white */
  padding: 15px; /* Padding for comfort */
  border-radius: 15px; /* Rounded corners */
  display: inline-block; /* Adjust to fit the content */
}

.start-button {
  background-color: #ff6f30; /* Warm orange */
  color: white; /* White text */
  font-family: sans-serif; /* Playful font */
  font-size: 1.5rem; /* Larger font size */
  padding: 10px 20px; /* Add some padding */
  border: none; /* No border */
  border-radius: 25px; /* Rounded corners */
  cursor: pointer; /* Pointer cursor */
  transition: background-color 0.3s ease; /* Smooth transition */
}

.start-button:hover {
  background-color: #ff4d00; /* Darker orange on hover */
  transform: scale(1.05); /* Slightly enlarge */
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3); /* Shadow effect */
}

.rules {
  font-size: 1rem; /* Increase font size */
  color: #4a4a4a; /* Dark gray */
  font-family: 'Arial', sans-serif; /* Clean font */
  margin-top: 20px; /* Space above */
  background-color: rgba(254, 180, 43, 0.6); /* Semi-transparent */
  padding: 15px; /* Padding for comfort */
  border-radius: 15px; /* Rounded corners */

}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.welcome,
.rules,
.countdown {
  animation: fadeIn 0.5s ease-in-out; /* Fade-in effect */
}
